import React, { Component } from 'react';
import { motion } from 'framer-motion';
import { aboutText } from './data';

import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// import Toolbar from '@mui/material/Toolbar';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
// import Divider from '@mui/material/Divider';
// import Link from '@mui/material/Link';

// import IconButton from '@mui/material/IconButton';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';

const anibox = {
	init: {},
	show: {transition:{staggerChildren:0.18}}
};
const anitem = {
	init: {opacity:0, x:-40},
	show: {opacity:1, x:0, transition:{duration:0.5}}
};
const anitem2 = {
	init: {opacity:0, y:-40},
	show: {opacity:1, y:0, transition:{duration:0.65}}
};



class About extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	componentDidMount() {}

	render() {
		return (
			<>
			<Grid
				container
				direction='column'
				justifyContent='center'
				alignItems='center'
				className='About'
			>
				<Container maxWidth='md'>
					<Box sx={{flexGrow:1}}>

						{/* 1. About me text, img */}
						<Stack direction='row' sx={{flexGrow:1, p:2}}>

							<Stack edge='start' sx={{flexGrow:1, pr:1}}>
								{/* 1-1. About Me title */}
								<Typography color='txt.bold' variant='h2' sx={{pr:2}}>
									About Me
								</Typography>
								{/* 1-2. About Me description */}
								<Typography sx={{flexGrow:1}}> </Typography>
								<motion.div
									variants={anibox}
									initial='init'
									whileInView='show'
								>
									<motion.div variants={anitem}>
										<Typography color='txt.main' sx={{pl:1, pb:1, pr:2}}> {aboutText[this.props.lang][0]} </Typography>
									</motion.div>
									<motion.div variants={anitem}>
										<Typography color='txt.main' sx={{pl:1, pb:1, pr:2}}> {aboutText[this.props.lang][1]} </Typography>
									</motion.div>
									<motion.div variants={anitem}>
										<Box sx={{p:0.5, pt:1}}>
											<Typography color='txt.main' sx={{display:'flex', alignItems:'center'}}> {aboutText[this.props.lang][2]} </Typography>
											<Typography color='txt.main' sx={{mt:0.5, display:'flex', alignItems:'center'}}> {aboutText[this.props.lang][3]} </Typography>
											<Typography color='txt.main' sx={{mt:0.5, display:'flex', alignItems:'center'}}> {aboutText[this.props.lang][4]} </Typography>
										</Box>
									</motion.div>
								</motion.div>
							</Stack>

							<Typography sx={{flexGrow:1}}> </Typography>

							{/* 1-3. Profile Image */}
							<Box justify='center'>
								<motion.div
									variants={anitem2}
									initial='init'
									whileInView='show'
								>
									<Box
										edge='end'
										component='img'
										sx={{height:300, width:300, border:4, borderColor:'txt.bold'}}
										alt="profile image."
										src='./unicorn.png'
									/>
								</motion.div>
								<Typography align='center' sx={{mt:1, fontSize:14, color:'txt.blurr'}}>
									{aboutText.pic[this.props.lang]}
								</Typography>
							</Box>

						</Stack>

						{/* 2. end, github and contact links */}
						{/*<Typography sx={{flexGrow:1}}></Typography>*/}
						<Stack direction='row' sx={{flexGrow:1}}>
							<Stack direction='row' sx={{p:1, mr:3}}>
								<Button sx={{color:'txt.bold'}} target='_blank' href='https://github.com/sunnibn'>
									<GitHubIcon /> <Box sx={{ml:2}}>github</Box>
								</Button>
							</Stack>
							<Stack direction='row' sx={{p:1, mr:3}}>
								<Button sx={{color:'txt.bold'}} href='mailto:sunnib8944@gmail.com'>
									<EmailIcon /> <Box sx={{ml:2}}>contact</Box>
								</Button>
							</Stack>
							{/* <Stack direction='row' sx={{p:1, mr:3}}>
								<Button sx={{color:'txt.bold'}} href=''>
									<LibraryBooksIcon /> <Box sx={{ml:2}}>personal study (notion)</Box>
								</Button>
							</Stack> */}
						</Stack>

					</Box>
				</Container>
			</Grid>
			</>
		);
	}
}

export default About;