import React, { Component } from 'react';
import Box from '@mui/material/Box';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Slide from '@mui/material/Slide';

import Drawer from '@mui/material/Drawer';
// import List from '@mui/material/List';
// import ListItem from '@mui/material/ListItem';
// import ListItemButton from '@mui/material/ListItemButton';
// import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
// import Button from '@mui/material/Button';

import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import LightModeIcon from '@mui/icons-material/LightMode';
import DarkModeIcon from '@mui/icons-material/DarkMode';

import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Stack from '@mui/material/Stack';

const styles = {
	navbarStyle: {
		bgcolor:'bg.n',
		color:'txt.n'
	},
	drawerStyle: {
		height:'100vh', 
		bgcolor:'bg.ndraw',
		borderLeft:4, 
		borderColor: 'bd.ndraw',
		color:'txt.ndraw',
	}
};

class Navbar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			drawerOn: false,
			navbarOn: true,
		};
		this.handleDrawerToggle = this.handleDrawerToggle.bind(this);
		this.wheelEvent = this.wheelEvent.bind(this);
	}

	handleDrawerToggle() {
		let flag = !this.state.drawerOn;
		this.setState({drawerOn: flag});
	}
	wheelEvent(event) {
		if (event.deltaY > 0) { //wheel down
			this.setState({navbarOn: false})
		} else {
			this.setState({navbarOn: true})
		}
	}

	componentDidMount() {
		window.addEventListener('wheel', this.wheelEvent, {passive: true});
	}

	render() {
		//=== theme(mode) icon setup.
		var modeIcon;
		if (this.props.mode === 'main') {
			modeIcon = <LightModeIcon />;
		} else {
			modeIcon = <DarkModeIcon />;
		}

		//=== drawer setup.
		var drawer = (
			<Box sx={{...styles.drawerStyle}}>
				{/*description, shortcuts*/}
				<Box sx={{p:3}} varient='dense'>
					<Stack direction='row'>
						<Typography edge='start' varient='h3' sx={{my:2, flexGrow:1}}>
							Dev. SB
						</Typography>
						<IconButton 
							onClick={()=>{this.handleDrawerToggle()}}
							edge='end'
							size='large'
							color='inherit'
							aria-label='menu'
						>
							<CloseIcon />
						</IconButton>
					</Stack>
					© 2023 Seoyoung Baek
				</Box>
				<Divider />
				{/*dark or light mode*/}
				<Box sx={{m:2}}>
					<IconButton 
						onClick={()=>{
							if (this.props.mode === 'main') {
								this.props.modefunc('dark');
							} else {
								this.props.modefunc('main');
							}
						}}
						edge='start' 
						size='large' 
						color='inherit' 
						aria-label='menu'
						sx={{m:1}}
					>
						{modeIcon}
					</IconButton>
					Light/Dark Mode
				</Box>
				<Divider />
				{/*kor or eng mode*/}
				{/* <Box sx={{m:3}}>
					<FormGroup>
						<Stack direction='row' spacing={1}>
						<Typography> Korean </Typography>
						<Switch defaultChecked onChange={(e)=>this.props.langfunc(e)} color='default'/>
						<Typography> English </Typography>
						</Stack>
					</FormGroup>
				</Box>
				<Divider /> */}
			</Box>
		);

		return (
			<>
			<Box sx={{flexGrow:1}}>
				<Slide direction='down' in={this.state.navbarOn}>
					<AppBar position='fixed' sx={{...styles.navbarStyle}} elevation={0}>
						<Toolbar varient='dense'>
							{/*left end*/}
							<Typography edge='start' variant='h6' sx={{flexGrow:1}}>Dev. SB's Portfolio</Typography>

							{/*middle space*/}
							<Typography sx={{flexGrow:1}}> </Typography>

							{/*right end*/}
							<IconButton 
								onClick={()=>{this.handleDrawerToggle()}}
								edge='end'
								size='large'
								color='inherit'
								aria-label='menu'
							>
								<MenuIcon />
							</IconButton>
						</Toolbar>
					</AppBar>
				</Slide>
			</Box>

			{/*Drawer area.*/}
			<Box sx={{}}>
				<Drawer
					anchor='right'
					variant='temporary'
					open={this.state.drawerOn}
					onClose={()=>{this.handleDrawerToggle()}}
					ModalProps={{keepMounted: true}}
					sx={{xs:'block', sm:'none'}}
				>
					{drawer}
				</Drawer>
			</Box>

			{/*Extra empty space for appbar.*/}
			{/*<Toolbar />*/}
			</>
		);
	}
}

export default Navbar;