import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import Navbar from './navbar';
import About from './about';
import Info from './info';
import Projs from './projs';

// import { aboutText, projsText } from './data';

// import { motion, useScroll, useSpring, useTransform, MotionValue } from 'framer-motion';
// import { TransitionGroup, CSSTransition } from 'react-transition-group';

import { createTheme, ThemeProvider } from '@mui/material/styles';
import Box from '@mui/material/Box';
// import Button from '@mui/material/Button';
// import Container from '@mui/material/Container';
// import Grid from '@mui/material/Grid';
// import Paper from '@mui/material/Paper';
import Toolbar from '@mui/material/Toolbar';

const maintheme = createTheme({
	palette: {
		primary: {
			main: '#558b2f',
		},
		bg: {
			main: '#aed581',
			n: '#558b2f',
			ndraw: '#aed581',
			categ: '#81c784',
			paper: '#9ccc65',
			dialog: '#9ccc65',
		},
		bd: {
			n: '#558b2f',
			ndraw: '#558b2f',
			categ: '#43a047',
			paper: '#689f38',
			dialog: '#689f38',
		},
		txt: {
			main: '#1b5e20',
			blur: '#43a047',
			blurr: '#7cb342',
			bold: '#00838f',
			n: '#f1f8e9',
			ndraw: '#33691e',
		}
	}
});
const darktheme = createTheme({
	palette: {
		primary: {
			main: '#263238',
		},
		bg: {
			main: '#263238',
			n: '#37474f',
			ndraw: '#263238',
			categ: '#37474f',
			paper: '#212121',
			dialog: '#263238',
		},
		bd: {
			n: '#00838f',
			ndraw: '#00838f',
			categ: '#00838f',
			paper: '#00838f',
			dialog: '#00838f',
		},
		txt: {
			main: '#b0bec5',
			blur: '#546e7a',
			blurr: '#455a64',
			bold: '#00b8d4',
			n: '#cfd8dc',
			ndraw: '#b0bec5',
		}
	}
});

const h = document.getElementsByTagName("html");



class App extends Component {
	constructor(props) {
		super(props);
		this.myRef = React.createRef();
		this.state = {
			mode: {
				name: 'main',
				theme: maintheme
			},
			lang: 'eng',
		};
		this.changeTheme = this.changeTheme.bind(this);
		this.changeLang = this.changeLang.bind(this);
	}

	changeTheme() {
		var property = this.state.mode;
		if (property.name === 'main') {
			property.name = 'dark';
			property.theme = darktheme;
			this.setState({property});
		} else {
			property.name = 'main';
			property.theme = maintheme;
			this.setState({property});
		}
	}
	changeLang(event) {
		if (event.target.checked) {
			this.setState({lang: 'eng'});
		} else {
			this.setState({lang: 'kor'});
		}
	}

	//async componentDidMount() {
		// await fetch('data.json')
		// .then((res) => res.json())
		// .then((res2) => {
		// 	this.state.data = res2;
		// 	this.setState(this.state.data);
		// })
		// .catch((err) => {
		// 	console.log(err.message);
		// });
	//}

	render() {
		return (
			<>
			<ThemeProvider theme={this.state.mode.theme}>
				<Box className='AppBox' sx={{bgcolor:'bg.main'}}>
					{/*0*/}
					<Navbar
						mode={this.state.mode.name}
						modefunc={this.changeTheme}
						lang={this.state.lang}
						langfunc={this.changeLang}
					/>
					{/*<Toolbar />*/}

					{/* 1 About */}
					<About
						lang={this.state.lang}
					/>

					{/* 2 Timeline & Skills */}
					<Toolbar />
					<Info
						lang={this.state.lang}
					/>

					{/* 3 Project Works */}
					<Toolbar />
					<Projs
						lang={this.state.lang}
					/>
					{/* end */}
				</Box>
			</ThemeProvider>
			</>
		);
	}
}

export default App;