import React, { Component } from 'react';
import { motion } from 'framer-motion';
import { projsText } from './data';

import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';

import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';

import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';

import CloseIcon from '@mui/icons-material/Close';
// import LinkIcon from '@mui/icons-material/Link';
import OpenInNewIcon from '@mui/icons-material/OpenInNew'; ////
import GitHubIcon from '@mui/icons-material/GitHub'; ////
import Icon from '@mui/material/Icon';

const styles = {
	categStyle: {
		mb:4, p:2,
		bgcolor:'bg.categ',
		border:3,
		borderRadius:2,
		borderColor:'bg.categ'
	},
	paperStyle: {
		bgcolor:'bg.paper',
		border:3,
		borderRadius:2,
		borderColor:'bd.paper',
		color:'txt.main',
	},
	dialogStyle: {
		paper: {
			bgcolor:'bg.dialog',
			color:'txt.main',
		},
		title: {
			bgcolor:'bg.dialog',
			borderTop:4,
			borderRight:4,
			borderLeft:4,
			borderColor:'bd.dialog',
			color:'txt.bold',
		},
		content: {
			bgcolor:'bg.dialog',
			borderRight:4,
			borderLeft:4,
			borderColor:'bd.dialog',
			color:'txt.main',
		},
		action: {
			bgcolor:'bg.dialog',
			borderRight:4,
			borderLeft:4,
			borderBottom:4,
			borderColor:'bd.dialog',
			color:'txt.bold',
		}
	},
	buttonStyle: {
		unselect: {
			color:'txt.main',
			border:2,
			mr:1,
			borderRadius:2,
			borderColor:'bg.categ'
		},
		select: {
			color:'txt.blur',
			border:2,
			mr:1,
			borderRadius:2,
			borderColor:'bd.categ'
		}
	}
};

const anibox = {
	init: {},
	show: {transition:{staggerChildren:0.25}},
	exit: {}
};
const anitem = {
	init: {opacity:0, y:10},
	show: {opacity:1, y:0, transition:{duration:0.6}},
	exit: {opacity:1, y:0}
};



class Projs extends Component {
	constructor(props) {
		super(props);
		this.projRef = React.createRef();
		this.state = {
			category: 'All',
			categorychange: false,
			content: undefined,
			opendialog: false,
		};
		this.changeCategory = this.changeCategory.bind(this);
		this.afterChangeCategory = this.afterChangeCategory.bind(this);
		this.setContent = this.setContent.bind(this);
		//render funcs
		this.renderCategory = this.renderCategory.bind(this);
		this.renderProjects = this.renderProjects.bind(this);
		this.renderContents = this.renderContents.bind(this);
	}
	changeCategory(title) {
		this.setState({category: title});
		this.setState({categorychange: true});
	}
	afterChangeCategory() {
		this.setState({categorychange: false});
	}
	setContent(proj) {
		var flag = this.state.opendialog;
		if (flag) {
			this.setState({opendialog: false});
			this.setState({content:undefined});
		} else {
			this.setState({opendialog: true});
			this.setState({content:proj});
		}
	}

	componentDidMount() {}

	renderCategory() {
		var catList = [];
		for (let i=0; i<projsText.category.length; i++) {
			let c = projsText.category[i];
			if (c === this.state.category) {
				catList.push( <Button sx={{...styles.buttonStyle.select}} key={c} onClick={()=>this.changeCategory(c)}>{c}</Button> );
			} else {
				catList.push( <Button sx={{...styles.buttonStyle.unselect}} key={c} onClick={()=>this.changeCategory(c)}>{c}</Button> );
			}
		}
		return catList;
	}
	renderProjects() {
		var projList = [];
		for (let i=0; i<projsText.projs.length; i++) {
			let p = projsText.projs[i];
			if (this.state.category==='All' || p.cat.includes(this.state.category)) {
				projList.push(
					<Grid key={i} item md={4}>
						<motion.div
							variants={anitem}
							whileHover={{scale:1.05}}
							whileTap={{scale:0.95}}
						>
							<Card onClick={()=>this.setContent(i)} sx={{...styles.paperStyle}}>
								<CardContent>
									<Typography variant='h6' color='txt.bold'> {p.title[this.props.lang]} </Typography>
									<Divider />
									<Box sx={{mt:0.5, color:'txt.bold', fontSize:12, textAlign:'right'}}>{p.time}</Box>
									<Box sx={{mt:0.5, fontSize:14}}>{p.desc[this.props.lang]}</Box>
								</CardContent>
								<CardActions sx={{m:1, color:'txt.bold', fontSize:12.5}}>
									{p.skill}
								</CardActions>
							</Card>
						</motion.div>
					</Grid>
				);
			}
		}
		return projList;
	}
	renderContents() {
		var projContent = [];
		if (this.state.content !== undefined) {
			var projObj = projsText.projs[this.state.content];
			projContent.push(
				<Dialog
					PaperProps={{
						sx: {bgcolor: 'bg.dialog'}
					}}
					fullWidth='true' maxWidth='sm'
					aria-labelledby='dialog-title'
					open={this.state.opendialog}
					onClose={() => this.setContent(this.state.content)}
				>
					{/* Title */}
					<DialogTitle sx={{...styles.dialogStyle.title}} id='dialog-title'>
						<Stack direction='row' sx={{display:'flex', alignItems:'center'}}>
							<Typography variant='h6' sx={{flexGrow:1}}>
								{projObj['title'][this.props.lang]}
							</Typography>
							<IconButton edge='end' onClick={()=>this.setContent(undefined)}>
								<CloseIcon sx={{color:'txt.bold'}} />
							</IconButton>
						</Stack>
					</DialogTitle>

					{/* Project Content */}
					<DialogContent sx={{...styles.dialogStyle.content}} dividers='true'>
						{projObj['content'][this.props.lang]}
					</DialogContent>

					{/* Links and External Sites */}
					<DialogActions sx={{...styles.dialogStyle.action}}>
						<IconButton color='inherit' sx={{m:1}} disabled>
							<Icon/>
						</IconButton>
						{projObj['link']}
					</DialogActions>

				</Dialog>
			);
		}
		return projContent;
	}




	render() {
		// Category
		var catList = this.renderCategory();
		// Projects Card
		var projList = this.renderProjects();
		// Project Content
		var projContent = this.renderContents();
		

		// Actual render
		return (
			<>
			<Box className='Projs'>

				{/*proj list*/}
				<Container maxWidth='md'>
					<Typography color='txt.bold' variant='h3'>Project Works</Typography>
					<Box sx={{mt:2, pb:4}}>

						{/* category */}
						<Box className='projcat' sx={{...styles.categStyle}}>
							{catList}
						</Box>

						{/* proj list | its' contents */}
						<motion.div
							variants={anibox}
							initial='init'
							whileInView='show'
							animate={this.state.categorychange ? 'show':'exit'}
							onAnimationComplete={() => this.afterChangeCategory()}
						>
							<Grid ref={this.projRef} container spacing={4}>
								{projList}
							</Grid>
						</motion.div>

					</Box>
				</Container>

				{/*proj content*/}
				{projContent}

			</Box>
			</>
		);
	}
}

export default Projs;